@import url('https://fonts.googleapis.com/css2?family=Prompt:wght@100;300;400;500;600;700;900&display=swap');




*{
  font-family: 'Prompt', sans-serif;
}
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #FD2667;
  background-blend-mode:luminosity; 
  background-image: url(https://images-ext-2.discordapp.net/external/3iIEtShLHTWBZJMHBJb11ZZ2VPsIEpX7fuA6j3-cxwc/https/cdn.pixabay.com/photo/2019/05/15/06/28/board-4204251_1280.png);
  background-repeat: repeat;
  color:white;
}
section{
  background-color: #111;
  padding-left: 1rem;
  padding-right: 1rem;
}


.navbar{
  background-color: rgba(1, 1,1, 1) !important;

}


.welcome{
  padding: 10rem 0rem 30rem 10rem;
  background-image: url(https://images-ext-2.discordapp.net/external/OIsJ9SFVyJ6aL2adOQeh5IoYhgY1g27xsgmQKlt9Joo/https/cdn.pixabay.com/photo/2022/06/06/08/02/background-7245525_1280.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size:cover;
  background-blend-mode:hard-light;
  background-color: #FD2667;
  
}
.text-red{
  color:#FD2667 !important;
}
.text-indigo{
  color:#3469F0 !important;
}
.link-red:hover{
  color:#3469F0 !important;
}
.border-red{
  border-color: #FD2667 !important;
}



.btn-explore{
  border: 2px solid #3469F0 !important;
  border-radius: 0px;
}
.btn-explore:hover{
  background-color: #3469F0 !important;
  color: white !important;
}


svg{
  opacity: 1!important;
}
.nav-link:hover{
  color:#3469F0 !important;
}
.text-shaded{
  text-shadow: 3px 3px #3469F0 !important;
}
.text-welcome{
  font-size:4.2rem !important;
  -webkit-text-stroke: 1px #3469F0;
}
.new-text{
  -webkit-text-stroke: 0.2px white;
  text-shadow: 2px 2px gray;
}
@media screen and (max-width) {
  
}


@media screen and (max-width:992px) {
  .welcome{
    min-height: 50em;
    padding: 10em 0em 0em 3em;
  }
  .text-welcome{
    font-size: 3.5rem !important;
  }
  .card{
    max-width:none !important;
   
  }
}

